<template>
  <div class="page">
    <!-- <div class="circle">
      <img src="../assets/index/start@2x.png" @click="goHome" />
    </div> -->
    <div class="mao-wrap">
      <img src="../assets/index/mao@2x.png" class="inner-img" />
    </div>
    <div class="mao-dao">
      <img src="../assets/index/mao_ying@2x.png" class="inner-img" />
    </div>
    <div class="start">
      <img
        src="../assets/index/start@2x.png"
        alt="开始探索"
        class="start-img"
      />
    </div>
    <div class="progress-wrap">
      <mt-progress
        class="ignore-mt-progress"
        :value="progressValue"
        :bar-height="15"
      >
      </mt-progress>
      <div class="font_mb">{{ progressValue }}%</div>
    </div>
  </div>
</template>

<script>
let that = null
export default {
  name: 'Loading',
  data () {
    return {
      progressValue: 0,
      timeInterval: null
    }
  },
  created () {
    that = this
    that.dynamicProgress()
    this.login()
  },
  methods: {
    dynamicProgress () {
      this.timeInterval = null
      this.timeInterval = setInterval(() => {
        if (that.progressValue < 90) {
          that.progressValue += 2
        } else {
          clearInterval(this.timeInterval)
          // this.goHome()
        }
      }, 10)
    },
    goHome () {
      // 判断是否首次进入
      let first = this.$store.getters.isNewer
      if (first) {
        this.$router.push('guide')
      } else {
        this.$router.push('home')
      }
    },
    async login () {
      // let res = await this.$request('post', 'activity/getrule', { type: 1 })
      let res = await this.$request('post', 'user/login', { mobile: '13111111111', fund_account: '111133', cid: '112', client_id: '112' }).finally(() => {
        this.progressValue = 100
        clearInterval(this.timeInterval)
      })
      // console.log('login:res:', res)
      // let res1 = {
      //   'error_no': '0',
      //   'error_info': 'SUCCESS',
      //   'data': {
      //     'uid': '202202261130000001',
      //     'cid': '111',
      //     'icon_type': null,
      //     'mobile': '13111111111',
      //     'client_id': '111',
      //     'fund_account': '111111',
      //     'lable': null,
      //     'nick_name': null,
      //     'role': '0',
      //     'leveal': '0',
      //     'total': 0,
      //     'available': 0
      //   }
      // }
      if (res && res.error_no === '0') {
        // 存储用户信息--
        this.$store.commit('setUserInfo', res.data)
        localStorage.setExpire('userInfo', res.data)
        console.log('userINfo:')
        console.log(this.$store.getters.userInfo)
        let timeout = null
        if (res.data && res.data.role > 0) {
          timeout = setTimeout(() => {
            this.progressValue = 100
            this.$router.push('home')
            clearTimeout(timeout)
          }, 500)
        } else {
          this.$router.push('guide')
        }
      } else {
        this.Toast(res.error_info ? res.error_info : '接口访问失败')
      }
    }
  }
}
</script>

<style scoped lang="less">
@import url("../assets/css/mixin.less");

.page {
  width: 100%;
  height: 100%;
  padding-top: 40px;
  // background: url(../assets/index/bg@2x.png) no-repeat;
  .bg-image("../assets/index/bg","png");
  background-size: 100% 100%;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  .circle {
    width: 420px;
    height: 450px;
    text-align: left;
    position: fixed;
    bottom: 45px;
    left: 0;
    > img {
      width: 420px;
      height: 450px;
    }
  }
}

.mao-wrap {
  width: 37.33%;
  /* height: 27.44%; */
  position: fixed;
  left: 28.8%;
  bottom: 15%;
  -webkit-animation: move1 3s ease-out;
  -moz-animation: move1 3s ease-out;
  animation: move1 3s ease-out;
  -webkit-animation-iteration-count: infinite;
  -moz-animation-iteration-count: infinite;
  animation-iteration-count: infinite;
}

.mao-dao {
  width: 34.67%;
  position: fixed;
  left: 29.07%;
  bottom: -18%;
  -webkit-animation: move2 3s ease-out;
  -moz-animation: move2 3s ease-out;
  animation: move2 3s ease-out;
  -webkit-animation-iteration-count: infinite;
  -moz-animation-iteration-count: infinite;
  animation-iteration-count: infinite;
}

.start {
  /* width: 420px; */
  /* height: 500px; */
  width: 56%;
  position: fixed;
  left: 0;
  bottom: 3.748%;
  /* z-index: 3; */
}

.start-img,
.inner-img {
  width: 100%;
  height: 100%;
}

.progress-wrap {
  width: 100%;
  height: 60px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  // position: relative;
  position: fixed;
  bottom: 30px;
}

@keyframes move1 {
  0% {
    transform: translateY(0px);
  }

  25% {
    transform: translateY(10px);
  }

  50% {
    transform: translateY(20px);
  }

  75% {
    transform: translateY(10px);
  }

  100% {
    transform: translateY(0px);
  }
}

@keyframes move2 {
  0% {
    transform: translateY(0px);
  }

  25% {
    transform: translateY(-10px);
  }

  50% {
    transform: translateY(-20px);
  }

  75% {
    transform: translateY(-10px);
  }

  100% {
    transform: translateY(0px);
  }
}
</style>
<style lang="less">
@import url("../assets/css/mixin.less");

.mt-progress,
.ignore-mt-progress {
  width: 80%;
  height: 27px;
  margin: 0 auto;
}

.mt-progress-runway {
  // background: url(../assets/index/progress_bg@2x.png) no-repeat;
  .bg-image("../assets/index/progress_bg","png");
  background-size: 100% 100%;
}
.mt-progress-progress {
  /* border-radius: 15px !important; */
  /* background: url(../assets/index/progress_active.png) no-repeat; */
  background-color: transparent;
  .bg-image("../assets/index/progress_active","png");
  background-size: 100% 100%;
  height: 15px;
}
.font_mb {
  font-size: 31px;
  -webkit-text-fill-color: white; /*文字的填充色*/
  -webkit-text-stroke: 1px #000; /*描边的像素，也就是粗细，这里指定是2像素的黑色边框*/
  position: absolute;
  top: 15px;
}
</style>



